/* global dataLayer */

const send = (event, options) => {
    if (typeof dataLayer === 'undefined') {
        return;
    }

    options = options || {};
    options.event = event;

    if (process.env.NODE_ENV === 'development') {
        console.log(
            JSON.stringify(options, undefined, 4)
        );
    }

    dataLayer.push(options);
}

export { send };